html, body {
  padding: 0;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  color: #4d4d4d;
  height: 100%; }

.main {
  height: 100%; }

.header, .footer {
  padding: 0 20px;
  color: #fff;
  background-color: #777777;
  height: 50px;
  line-height: 50px; }

.body {
  height: calc(100% - 100px);
  padding: 20px;
  box-sizing: border-box; }

.container {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px; }

h2 {
  font-size: 26px;
  margin: 20px 0;
  text-align: center; }
  h2 small {
    font-size: 0.5em; }

.responsive-table li {
  border-radius: 3px;
  padding: 25px 30px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px; }

.responsive-table .table-header {
  background-color: #95A5A6;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.03em; }

.responsive-table .table-row {
  background-color: #ffffff;
  box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1); }

.responsive-table .col-1 {
  flex-basis: 10%; }

.responsive-table .col-2 {
  flex-basis: 40%; }

.responsive-table .col-3 {
  flex-basis: 25%; }

.responsive-table .col-4 {
  flex-basis: 25%; }

@media all and (max-width: 767px) {
  .responsive-table .table-header {
    display: none; }
  .responsive-table li {
    display: block; }
  .responsive-table .col {
    flex-basis: 100%; }
  .responsive-table .col {
    display: flex;
    padding: 10px 0; }
    .responsive-table .col:before {
      color: #6C7A89;
      padding-right: 10px;
      content: attr(data-label);
      flex-basis: 50%;
      text-align: right; } }
